@import "../../global.scss";

.topbar {
  width: 100%;
  height: $topbarHeight;
  color: $mainColor;
  // outline: 1px solid coral;
  background-color: white;
  transition: all 0.3s ease;
  z-index: 3;
  position: fixed;
  top: 0;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      // background-color: orange;
      display: flex;
      align-items: center;

      .logo {
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;
      }

      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 30px;
        @include mobile {
          display: none;
        }
        .icon {
          font-size: 25px;
          margin-right: 10px;
        }
        span {
          font-size: 19px;
          font-weight: 400;
        }
      }
    }

    .right {
      // outline: 1px solid blue;
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        overflow: hidden;
        span {
          width: 100%;
          height: 3px;
          background-color: $mainColor;
          transition: all 0.3s ease;
        }
      }
    }
  }

  &.active {
    background-color: $mainColor;
    color: white;

    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
