@import "../../global.scss";

.portfolio {
  background-color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0px;
  @include mobile {
    padding: 20px 0px;
  }

  h1 {
    font-size: 50px;
    margin-bottom: 20px;
    @include mobile {
      font-size: 50px;
      margin-bottom: 50px;
    }
  }

  ul {
    margin: 40px 10px;
    padding: 0;
    list-style: none;
    display: flex;
    @include mobile {
      margin: 0px 20px 30px 20px;
      justify-content: center;
      flex-wrap: wrap;
      align-items: center;
      height: 10vh;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @include mobile {
      width: 100%;
    }

    .item {
      width: 220px;
      height: 150px;
      border-radius: 10px;
      border: 1px solid rgb(240, 240, 240);
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;
      padding: 3px;
      overflow: hidden;

      @include mobile {
        width: 130px;
        height: 100px;
      }

      h3 {
        position: absolute;
        font-size: 20px;
        padding: 50px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
